import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ListComponent } from './components/basic/list/list.component';


const routes: Routes = [
  {
    path: 'basic',
    loadChildren: () => import('./components/basic/basic.module').then(m => m.BasicModule)
  },
  {
    path: 'business',
    loadChildren: () => import('./components/business/business.module').then(m => m.BusinessModule)
  },

  {
    path: ':type',
    loadChildren: () => import('./components/admins/admins.module').then(m => m.AdminsModule)
  },
  // { path: 'admin', component: AdminUserListComponent }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class UserManagementRoutingModule { }
